.apology-section {
	position: relative;
	padding: 5rem 0;
	background-color: var(--color-primary);
	color: var(--color-dark);
	@include max-md {
		padding: 3rem 0;
	}
	.frame {
		position: absolute;
		top: 3rem;
		left: 50%;
		width: 100%;
		transform: translate(-50%, 0);
	}
	.sky {
		position: absolute;
		bottom: 0;
		left: 50%;
		width: 100%;
		transform: translate(-50%, 20%) scaleX(-1);
		opacity: 0.6;
	}
	.appology-wrap {
		position: relative;
		padding: 3rem 2rem;
		@include max-md {
			padding: 2rem 1rem;
		}
		.title-section {
			padding: 0;
		}
	}
}
