.story-section {
	position: relative;
	.story {
		position: relative;
		padding: 5rem 0;
		@include max-md {
			padding: 3rem 0;
		}
	}
	.story-content {
		position: relative;
	}
	.story-description {
		position: relative;
		text-align: center;
	}
	.image-wrapper {
		position: relative;
		width: 100%;
		height: auto;
		aspect-ratio: 3/4;
		margin-top: 2rem;
		.image-element {
			position: relative;
			width: 100%;
			height: 100%;
			.story-background {
				object-fit: cover;
				width: 100%;
				height: 100%;
				border-radius: var(--border-radius);
			}
		}
		.frame {
			position: absolute;
			top: -2px;
			left: -1px;
			width: calc(100% + 3px);
			path {
				fill: var(--color-main);
			}
		}
	}
}
